@import url(https://fonts.googleapis.com/css?family=Ubuntu:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand:600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.TopName {
  background-color: #282c34;
  text-align: right;
  color: whitesmoke;
  vertical-align: middle;
  font-size: 5mm;
  font-weight: bold;
  font-family: 'Helvetica Neue';
  padding-top: 1mm;
  padding-bottom: 2mm;
  padding-right: 3mm;
}

.navbar {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  justify-content: left;
  color: whitesmoke;
  vertical-align: middle;
  align-items: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li,
a,
button,
div {
  font-family: 'Ubuntu';
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
}

header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 15px;
  background-color: #0a1e2c;
}

.name {
  color: whitesmoke;
  font-size: 20px;
  font-family: 'Ubuntu';
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  vertical-align: middle;
}

.seal {
  max-width: 55px;
  height: auto;
  vertical-align: middle;
  text-align: flex-end;
  padding-right: 20px;
}

.navlinks {
  list-style: none;
}

.navlinks li {
  display: inline-block;
  justify-content: flex-start;
  padding: 0px 10px;
}

.navlinks li a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.navlinks li a:hover {
  color: rgba(101, 160, 177, 0.8);
}

button {
  margin-left: 20px;
  padding: 9px 25px;
  background-color: rgb(150, 181, 211);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.contact {
  margin-inline: auto;
}

button:hover {
  background-color: rgba(101, 160, 177, 0.8);
}

.divider {
  height: 1px;
  background-color: rgb(150, 181, 211);
  opacity: 0.4;
}

.textHeading {
  font-family: 'Quicksand', sans-serif;
  font-size: 60px;
  color: rgba(65, 136, 194, 0.8);
  text-align: left;
  padding-left: 50px;
  padding-top: 50px;
}

.textBody {
  font-family: 'Roboto';
  font-size: 20px;
  color: black;
  padding-left: 100px;
  padding-bottom: 50px;
  padding-right: 100px;
  padding-top: 20px;
}

.contactInfo {
  font-family: 'Roboto';
  color: rgba(72, 96, 116, 0.8);
  font-size: 20px;
  border-bottom: 1px dotted #000;
  text-decoration: none;
}

#slideshow {
  margin: 50px auto;
  position: relative;
  color: black;
  width: 400px;
  height: 300px;
  padding: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

#slideshow > div {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 10px;
  bottom: 10px;
}

